<script>
import { getDaily } from "@/service/spider";
import { getMeirentu } from "@/service/spider";
import servers from "@jx3box/jx3box-data/data/server/server_cn.json";
import User from "@jx3box/jx3box-common/js/user";
export default {
    name: "daily",
    props: [],
    data: function() {
        return {
            year: "",
            month: "",
            date: "",

            daily: [],

            meirentu: "",
            servers,
            server: "",
        };
    },
    computed: {
        default_server: function() {
            return this.$store.state.server;
        },
    },
    methods: {
        initDate: function() {
            let dt = new Date();
            this.year = dt.getFullYear();
            this.month = dt.getMonth() + 1;
            this.date = dt.getDate();
        },
        loadDaily: function() {
            let dt = new Date();
            let hour = dt.getHours();
            let q;
            if (hour > 0 && hour < 9) {
                q = ~~((Date.now() - 86400000) / 1000);
            } else {
                q = ~~(Date.now() / 1000);
            }

            getDaily(q).then((res) => {
                let list = res.data.data;
                list.forEach((item) => {
                    if (item.taskType == "大战") {
                        this.daily.push({
                            type: "大战",
                            zone: "全服",
                            name: item.activityName.slice(3, -1),
                        });
                    } else {
                        this.daily.push({
                            type: "战场",
                            zone: "全服",
                            name: item.activityName.slice(3),
                        });
                    }
                });
            });
        },
        loadMeirentu: function() {
            getMeirentu(this.server).then((res) => {
                this.meirentu = res.data.data;
            });
        },
    },
    mounted: function() {
        this.server = this.default_server;
        this.initDate();
        // this.loadDaily();
        // this.loadMeirentu();
    },
    // watch: {
    //     server: function(val) {
    //         val && this.loadMeirentu();
    //     },
    // },
};
</script>

